.friendMessage {
    display: flex;
    flex-direction: column;
    margin: 47px 0 0 70px;

}

.friendImageAndText {
    display: flex;
    justify-content: flex-start;
    align-items: center;

}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 48px;
    background:  lightgray -16.029px 0 / 150.121% 100% no-repeat;
}

.friendText {
    margin-left: 10px;
    border-radius: 11px;
    max-height: 60px;
    flex-shrink: 0;
    background-color: #F5F7FB;
    filter: drop-shadow(0px 1px 2px rgba(29, 33, 38, 0.10)) drop-shadow(0px 5px 20px rgba(29, 33, 38, 0.03));
    position: relative;
}

.friendText:after {
    left: -12px;
    position: absolute;
    content: '';
    transform: rotate(-5deg);
    bottom: -1.4px;
    width: 0;
    height: 0;
    border-top: 14px solid transparent;
    border-bottom: 2px solid transparent;
    border-right: 21px solid #F5F7FB;

;
}

.friendName {
    margin: 7px 0 4px 17px;
    color: #000;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
}

.friendMessageText {
    color: #000;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

    margin: 3px 13px 8px 17px;
}

.friendTime {
    color: #000;
    font-family: Montserrat;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-left: 12px;
}
