.message {
    display: flex;
    flex-direction: column;
    padding-left: 13px;
    margin-right: 70px;
    max-width:100%;
    width: 1280px;
}

.imageAndText {
    display: flex;
    justify-content: end;
    align-items: center;
    flex-direction: row-reverse;
    margin: 36px 70px 0px 0;

}

.imageAndText img {
    max-width:100%;
    width: 48px;
    height: 48px;
    flex-shrink: 0;
    border-radius: 48px;
    background: lightgray -16.029px 0px / 150.121% 100% no-repeat;

}

.text {
    max-width: 527px;
    height: 60px;
    flex-shrink: 0;
    background-color: #06C;
    filter: drop-shadow(0px 1px 2px rgba(29, 33, 38, 0.10)) drop-shadow(0px 5px 20px rgba(29, 33, 38, 0.03));
    border-radius: 11px;
    margin-right: 10px;
    position: relative;

}

.text::after {
    transform: rotate(5deg);
    bottom: -1.4px;
    position: absolute;
    content: '';
    right: -12px;
    width: 0;
    height: 0;
    border-top: 14px solid transparent;
    border-bottom: 2px solid transparent;
    border-left: 21px solid #06C;
}

.name {
    color: #FFF;;
    font-family: Montserrat;
    font-size: 18px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    display: flex;
    flex-direction: row-reverse;
    margin:7px 15px 4px 13px;
}

.messageText {

    color: #FFF;;
    font-family: Montserrat;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    align-items: center;
    display: flex;
    justify-content: end;
    margin:3px 15px 8px 13px;
}

.time {
    color: #000;
    font-family: Montserrat;
    font-size: 10px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    display: flex;
    flex-direction: row-reverse;
    margin: -5px 82px 0px 0;

}