.sendForm {


    display: flex;
    align-items: center;
    margin: 73px 70px 0 0px;
}

.textarea {
resize: none;
    width: 976px;
    max-width: 100%;
    min-height: 40px !important;
padding: 10px 24px;
    border-radius: 20px;
    background: #F5F7FB;
    box-shadow: 0 5px 20px 0 rgba(29, 33, 38, 0.03), 0 1px 2px 0 rgba(29, 33, 38, 0.10);
    border: none;
    margin-left: 70px;

}
::placeholder{
    color: rgba(0, 0, 0, 0.60);
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;

}

.button {
    justify-content: center;
    align-items: center;
    width: 120px;
    height: 40px;
    flex-shrink: 0;
    border-radius: 20px;
    background: #06C;
    color: #FFF;
    font-family: Montserrat sans-serif;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    border: none;
    margin: 0px 70px 0px 44px
}

.button:hover {
    background: #0553a1;
}

.button:active {
    background: #3b97f3;
}
